$pg-background: hsl(0, 50%, 50%);



#app-container,
#app-container * {

  box-sizing: border-box;

}

#app-container {

  height: 100vh;
  width: 100vw;
  background: $pg-background;
  border: 1px solid $pg-background;
  padding: 0;

}

#temp-content {

  margin: .5em;
  padding: 0;

}